'use client';

import { Compiler } from '@/types/api/compiler';
import { Language } from '@/types/api/language';
import {
  Select,
  SelectContent,
  SelectGroup,
  SelectItem,
  SelectLabel,
  SelectTrigger,
  SelectValue,
} from '../ui/select';
import { CompilerSelect } from './compiler-select';

export function LangCompilerSelect({
  langs,
  lang,
  compiler,
  setLang,
  setCompiler,
}: {
  langs: Language[];
  lang: Language;
  compiler: Compiler;
  setLang(lang: Language): void;
  setCompiler(comp: Compiler): void;
}) {
  return (
    <div className='rouned-2xl flex items-center justify-center gap-5'>
      <Select
        onValueChange={v => {
          setLang(langs.find(l => l.name === v) ?? langs[0]);
          setCompiler(
            langs.find(l => l.name === v)?.compilers[0] ??
              langs[0].compilers[0],
          );
          console.log('lang change');
        }}
      >
        <SelectTrigger className='w-[180px] bg-white!'>
          <SelectValue defaultValue={lang.name} />
        </SelectTrigger>
        <SelectContent>
          <SelectGroup>
            <SelectLabel>Языки</SelectLabel>
            {langs.map(l => (
              <SelectItem key={l.id} value={l.name}>
                {l.name}
              </SelectItem>
            ))}
          </SelectGroup>
        </SelectContent>
      </Select>
      <CompilerSelect
        compiler={compiler}
        lang={lang}
        setCompiler={setCompiler}
      />
    </div>
  );
}
